.horizontal-line {
    border-bottom: 2px solid white;
    box-shadow: 0 0 70px 20px rgba(255, 255, 255, 0.3);
  
}

.about-main{
    display:flex;
    flex-direction: column;
}

.about {
    color: white;
    display:flex;
    justify-content: center;
    font-size: 1.5rem;
    letter-spacing: 2px;
    

}

.aboutDiv {
    margin: 40px 0 40px 0;
}

.photoDiv{
    display: flex;
    justify-content: center;
    margin-right: 15px;
}


.photo {

    box-shadow: 0px 3px 74px -4px #8f0202;
    height: 31rem;
    width: 30rem;
    height: auto;
    display: flex;
}

.h1-text {
    font-size: 1rem;
    letter-spacing: 2px;
    color:white;
}

.textAndImg {
    display: flex;
}

.textDiv {
    margin: 30px;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

@media only screen and (min-width: 1075px) {
    .textDiv{
        margin: 30px;
    }
}
